.offer-main {
  .details-banner-img {
    .detail-title {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 2;
    }
    .layer{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: rgba(0,0,0,0.5);
      height: 100%;
      width: 100%;
      z-index: 1;
    }
  }
}
.flat-text{
  p{
    position: relative;
    height: 35px;
    padding-left: 35px;
    padding-right: 10px;
    background: #0AAF51;
    display: flex;
    align-items: center;
    justify-content: center;
    &:after{
      content: '';
      position: absolute;
      left: -2px;
      width: 0;
      height: 0;
      border-top: 20px solid transparent;
      border-left: 20px solid var(--white);
      border-bottom: 20px solid transparent;
    }
  }
}
.offer-button{
  width: 220px !important;
  height: 55px !important;
}
.coupon-code{
  width: 120px;
  height: 44px;
  border: 1px dashed #FFC86B;
  border-radius: 10px;
}